.landing {

	header {
		background: transparent;
		position: absolute;
		top: 31px;
		width: 100%;
		z-index: 3;
	}
}

.section--landing-hero {
	padding: 100px 0;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;

	.wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.left {
			width: 50%;
			order: 0;
			
			@include media-breakpoint-up(lg) {
				position: relative;
				left: 100px;
			}

			@include media-breakpoint-down(sm) {
				width: 100%;
				order: 1;
			}
		}

		.right {
			width: 50%;
			order: 1;

			img {
				max-width: 100%;

				@include media-breakpoint-down(sm) {
					max-width: initial;
					height: auto;
					width: 100%;
				}
			}

			@include media-breakpoint-down(sm) {
				width: 100%;
				order: 0;
				text-align: center;
			}
		}
	}

	.caption {
		color: $foreground-secondary;
		max-width: 500px;

		@include media-breakpoint-down(sm) {
			max-width: 100%;
		}

		@include media-breakpoint-down(xs) {
			text-align: center;
		}

		.title {
			font-size: 60px;
			line-height: 64px;
			font-weight: $font-semibold;
			margin: 20px 0;

			i {
				color: $foreground-primary;
			}

			@include media-breakpoint-down(sm) {
				font-size: 42px;
				line-height: 52px;
			}

			@include media-breakpoint-down(sm) {
				font-size: 32px;
				line-height: 42px;
			}
		}

		.description {
			font-size: 18px;
			font-weight: $font-regular;
			line-height: 30px;

			@include media-breakpoint-down(xs) {
				font-size: 16px;
				line-height: 26px;
			}
		}

		.cta {
			display: flex;
			justify-content: flex-start;
			margin-top: 50px;

			@include media-breakpoint-down(xs) {
				justify-content: center;
			}
	
			.icon--arrow-circle-right { 
				width: 50px; 
				height: 50px;
				fill: $foreground-primary;
				margin-right: 20px;
				transform: rotate(90deg);
			}
	
			a {
				color: $foreground-primary;
				text-transform: uppercase;
				font-weight: $font-semibold;
				font-size: 14px;
				letter-spacing: 0.5px;
				display: flex;
				align-items: center;

			}
		}
	}
}

.section--landing-categories {
	padding: 20px 0;
	border-bottom: 1px solid #dbdbdb;
	text-align: center;
	overflow-x: auto;

	@include media-breakpoint-down(md) {
		padding: 10px 0;
	}

	ul {
		list-style: none;
		padding: 0;
		margin: 0;

		@include media-breakpoint-down(sm) {
			width: 1000px;
		}

		li {
			display: inline-block;
			padding: 0 30px;

			@include media-breakpoint-down(md) {
				padding: 0 20px;
			}

			a {
				font-weight: 600;
				color: #7f7f7f;

				@include media-breakpoint-down(md) {
					font-size: 14px;
				}

				&:hover, &:focus {
					color: $foreground-primary;
				}
			}
		}
	}
}

.section--landing-listing {
	padding: 100px 0;

	.intro {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		margin-bottom: 100px;
		margin-top: 100px;

		.title {
			font-weight: 600;
			font-size: 36px;
			line-height: 54px;
			color: #e10000;
		}
	}

	.row {
		align-items: center;

		.col-12 {
			padding: 100px 0;
			
			@include media-breakpoint-down(xs) {
				padding: 50px 0;
			}
		}
	}

	.item {
		position: relative;
		text-align: center;
		min-height: 640px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		flex-direction: column;
		justify-content: center;		
		align-items: center;

		@include media-breakpoint-down(sm) {
			min-height: initial;
			flex-direction: row;
			flex-wrap: wrap;
			text-align: left;
			justify-content: space-around;
		}

		.img {
			position: absolute;
			z-index: 3;

			@include media-breakpoint-down(sm) {
				position: relative;
			}

			img {
				max-width: 100%;

				@include media-breakpoint-down(xs) {
					max-width: 40px;
				}
			}

			&--top {
				position: absolute;
			}
		}

		.title {
			color: $foreground-primary;
			font-weight: 600;
			font-size: 60px;
			letter-spacing: 0.2px;
			line-height: 64px;
			z-index: 2;
			position: relative;

			@include media-breakpoint-down(sm) {
				width: 100%;
			}

			@include media-breakpoint-down(xs) {
				font-size: 32px;
				line-height: 42px;
			}

			&--baby {
				color: #19aad1;
			}

			&--professional {
				color: $foreground-secondary;

				span {
					color: #fff;
					text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
				}
			}
		}

		.description {
			color: $foreground-secondary;
			font-size: 40px;
			font-weight: 400;
			line-height: 44px;
			letter-spacing: 0.2px;
			z-index: 2;
			position: relative;
			margin: 20px 0 40px;

			@include media-breakpoint-down(sm) {
				width: 100%;
			}

			@include media-breakpoint-down(xs) {
				font-size: 24px;
				line-height: 34px;
			}
		}

		.cta {
			position: relative;
			z-index: 4;

			@include media-breakpoint-down(sm) {
				width: 100%;
			}

			@include media-breakpoint-down(xs) {
			}

			a {
				color: $foreground-primary;
				text-transform: uppercase;
				font-size: 16px;
				line-height: 20px;
				letter-spacing: 0.5px;
				font-weight: 600;
			}
		}

		&--promozione {
			border: 2px solid $foreground-primary;
			height: auto;
			min-height: initial;
			padding: 40px;

			.abstract {
				color: $foreground-primary;
				text-transform: uppercase;
				font-size: 16px;
				line-height: 20px;
				letter-spacing: 0.5px;
				font-weight: 600;
			}

			.img {
				position: relative;

				img {
					max-width: 100%;
				}
			}

			.title {
				font-size: 40px;
				line-height: 44px;
				letter-spacing: 0.2px;
			}

			.description {
				font-size: 18px;
				line-height: 36px;
				letter-spacing: 0.2px;

				span {
					color: $foreground-primary;
				}
			}
		}

		&--magazine {
			position: static;
			min-height: initial;
			display: block;
			text-align: left;

			.box {
				background-size: cover !important;
				min-height: 700px;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: end;
				-ms-flex-align: end;
				align-items: flex-end;

				@include media-breakpoint-down(md) {
					min-height: 450px;
				}
		
				@include media-breakpoint-down(sm) {
					margin-bottom: 30px;
				}

				.black-layer {
					content: '';
					display: block;
					height: 200px;
					width: 100%;
					position: relative;
					z-index: 1;
					bottom: 0;
					left: 0;
					background: rgba(0, 0, 0, 0);
					background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
					background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0)), color-stop(100%, rgba(0, 0, 0, 1)));
					background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
					background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
					background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
					background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
					filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#000000', endColorstr='#000000', GradientType=0);
				}

				.caption {
					position: absolute;
					z-index: 2;
					padding: 30px;
		
					.title {
						color: #fff;
						font-size: 40px;
						font-weight: $font-medium;
		
						@include media-breakpoint-down(xs) {
							font-size: 32px;
							line-height: 42px;
						}
					}
		
					.description {
						color: #fff;
						font-size: 18px;
						line-height: 36px;
						letter-spacing: 0.2px;
						margin: 0;

						@include media-breakpoint-down(xs) {
							font-size: 16px;
							line-height: 26px;
						}
		
						a {
							color: $foreground-primary;
							border-bottom: 2px solid $foreground-primary;
						}
					}
				}
			}
		}
	}

	.more {
		text-align: center;
	}
}